body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Montserrat', sans-serif;
}

.content{
  min-height: calc(100vh - 100px - 64px);
}

.preloader_main{
  position: absolute;
  bottom: 50%;
  right: 50%;
}

.goods{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 25px;
}

.mistake{
  padding-top: 2em;
}

.cart {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  cursor: pointer;
  border-radius: 5%;
  z-index: 5;
  padding: 1rem;
  -webkit-border-radius: 5%;
  -moz-border-radius: 5%;
  -ms-border-radius: 5%;
  -o-border-radius: 5%;
}
@media (min-width: 767px) {
  .cart{
    bottom: unset;
    top: 5rem;
  }
}
@media (max-width: 767px) {
  .goods{
    grid-template-columns: 1fr;
  }
}

.material-icons{
  font-size: 35px;
}

.basket-list{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 50rem;
  max-width: 95%;
  box-shadow: 1px 3px 8px 5px #4dd0e1;
  animation: show 500ms ease-in-out;
  -webkit-animation: show 500ms ease-in-out;
}

.basket-quantity{
  cursor: pointer;
  vertical-align: middle;
  color: #4dd0e1;
  font-size: 20px;
}

.card-title{
  font-size: 16px;
}

.basket-clear{
  cursor: pointer;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  color: #fff;
}

@keyframes show {
  from {
    top: 70%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}



#toast-container {
  top: 3rem !important;
  animation: fade-in 250ms ease-in-out;
  -webkit-animation: fade-in 250ms ease-in-out;
}
@keyframes fade-in{
  from {
    transform: translateY(2rem);
    opacity: 0;
    -webkit-transform: translateY(2rem);
    -moz-transform: translateY(2rem);
    -ms-transform: translateY(2rem);
    -o-transform: translateY(2rem);
  }
  to {
    transform: translateY(0rem);
    opacity: 1;
    -webkit-transform: translateY(0rem);
    -moz-transform: translateY(0rem);
    -ms-transform: translateY(0rem);
    -o-transform: translateY(0rem);
  }
}
